body {
  margin: 0;
}

.btn {
  display: inline-block;
  background: #ff5722;
  color: #fff;
  font-family: sans-serif;
  font-size: inherit;
  line-height: 1.2;
  padding: 0.5rem 1rem;
  border: 0;
  margin: 0.5rem;
  border-radius: 0.1875rem;
  cursor: pointer;
}

.pop{
  background-color: grey;
}

.btn:hover,
.btn:focus {
  background: #e64a19;
}



